<template>
  <div>
    <transition :name="direction">
      <div class="box"
           v-show="flag"></div>
    </transition>
    <button @click="qie">切换</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      direction: '',
      flag: true
    }
  },
  methods: {
    qie () {
      if (this.flag) {
        this.direction = 'move'
      } else {
        this.direction = 'back'
      }
      this.flag = !this.flag
    }
  }
}
</script>

<style lang="scss" scoped>
.move-enter-active,
.move-leave-active {
  transition: all 0.5s;
}
.move-enter {
  transform: translateX(100%);
}
.move-enter-to,
.move-leave {
  transform: translateX(0);
}
.move-leave-to {
  transform: translateX(100%);
}

.back-enter-active,
.back-leave-active {
  transition: all 0.5s;
}
.back-enter {
  transform: translateX(100%);
}
.back-enter-to,
.back-leave {
  transform: translateX(0);
}
.back-leave-to {
  transform: translateX(100%);
}
.box {
  width: 200px;
  height: 200px;
  background-color: red;
}
</style>
